<template>
  <b-container fluid>
    <b-row>
      <b-col>
        <b-dropdown id="build-config" :text="'buildConfigList'">
          <b-dropdown-item v-for="item in buildConfigs.items"
            :key="item.metadata.name"
            :value="item.metadata.name">
            {{item.metadata.name}} [Last Build#{{item.status.lastVersion}}]
            <b-button @click="startBuild(item.metadata.name)">Start Build</b-button>
            <b-button @click="delBuildConfig(item.metadata.name)">Delete BuildConfig</b-button>
            <b-button @click="showImageStream(item.metadata.name)">Get ImageStream</b-button>
          </b-dropdown-item>
        </b-dropdown>
        <br>
        <b-dropdown id="build" :text="'buildList'">
          <b-dropdown-item v-for="item in builds.items"
            :key="item.metadata.name"
            :value="item.metadata.name">
            {{item.metadata.name}} from [{{item.status.config.name}}]
            <b-button @click="createDeploy(item.metadata.name)">Create Deploy</b-button>
            <b-button @click="delBuild(item.metadata.name)">Delete Build</b-button>
            <b-button @click="getDetail(item.metadata.name)">View Detail</b-button>
            <b-button @click="getLog(item.metadata.name)">View Log</b-button>
          </b-dropdown-item>
        </b-dropdown>
        <br>
        <b-dropdown id="deploy-config" :text="'deployConfigList'">
          <b-dropdown-item v-for="item in deployConfigs.items"
            :key="item.metadata.name"
            :value="item.metadata.name">
            {{item.metadata.name}}  [Last Version#{{item.status.latestVersion}}]
            <b-button @click="startDeploy(item.metadata.name)">시작</b-button>
            <b-button @click="delDeployConfig(item.metadata.name)">Delete DeployConfig</b-button>
          </b-dropdown-item>
        </b-dropdown>
        <br>
        <b-dropdown id="pod" :text="'Running podList'">
          <b-dropdown-item v-for="item in runningPods"
            :key="item.metadata.name"
            :value="item.metadata.name">
            {{item.metadata.name}}
            <b-button @click="pauseDeploy(item)">정지</b-button>
            <b-button @click="getPodDetail(item.metadata.name)">View Detail</b-button>
            <b-button @click="getPodLogMsg(item.metadata.name)">View Log</b-button>
          </b-dropdown-item>
        </b-dropdown>
        <br>
        <b-dropdown id="template" :text="'TemplateList'">
          <b-dropdown-item v-for="item in rosTemplate"
            :key="item.metadata.name"
            :value="item.metadata.name">
            {{item.metadata.name}}
            <b-button @click="testTemplate(item.metadata.name)">Get Template</b-button>
            <b-button @click="createBuild(item.metadata.name)">Create BuildConfig</b-button>
          </b-dropdown-item>
        </b-dropdown>
      </b-col>
      <b-col>
        <b-form-textarea
          id="logarea"
          v-model="logText"
          placeholder=""
          rows="30"
          max-rows="30"
        ></b-form-textarea>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapActions } from 'vuex'
import createBuildConfigForm from '../assets/json/createBuildConfig.json'
import createDeployConfigForm from '../assets/json/createDeployConfig.json'

export default {
  name: 'apitest',
  props: {
    projectName: { type: String, default: () => '' }
  },
  data () {
    return {
      buildConfigs: { items: [] },
      builds: { items: [] },
      deployConfigs: { items: [] },
      pods: { items: [] },
      templates: { items: [] },
      images: { items: [] },
      logText: ''
    }
  },
  computed: {
    runningPods () {
      return this.pods.items.filter(item => {
        return (item.status.phase === 'Running' || item.status.phase === 'Pending')
      })
    },
    rosTemplate () {
      return this.templates.items.filter(item => {
        if (item.labels && item.labels.template) {
          return item.labels.template.includes('ros')
        }
        return false
      })
    }
  },
  mounted () {
    this.requestLists()
  },
  methods: {
    ...mapActions('build', [
      'getBuildConfigList',
      'createBuildConfig',
      'deleteBuildConfig',
      'getBuildList',
      'instantBuild',
      'watchBuild',
      'deleteBuild',
      'getBuildLog']),
    ...mapActions('deploy', [
      'getDeployConfigList',
      'createDeployConfig',
      'instantDeploy',
      'editDeployConfig',
      'getDeployConfig',
      'deleteDeployConfig',
      'deleteReplicationController']),
    ...mapActions('pod', [
      'getPodList',
      'getPodLog',
      'deletePod']),
    ...mapActions('template', [
      'getTemplateList',
      'getTemplate']),
    ...mapActions('imagestream', ['getImageStream']),
    requestLists () {
      this.getBuildConfigList(this.projectName).then((list) => {
        this.buildConfigs = list
        // this.watchBuild(this.projectName)
      })
      this.getBuildList(this.projectName).then((list) => {
        this.builds = list
      })
      this.getDeployConfigList(this.projectName).then((list) => {
        this.deployConfigs = list
      })
      this.getPodList(this.projectName).then((list) => {
        this.pods = list
      })
      this.getTemplateList().then((list) => {
        this.templates = list
        window.templates = list
      })
    },
    isRunning (item) {
      return true
    },
    startBuild (buildName) {
      this.instantBuild({
        projectName: this.projectName,
        buildName: buildName
      })
    },
    startDeploy (buildName) {
      this.getDeployConfig({
        projectName: this.projectName,
        buildName: buildName
      }).then((response) => {
        this.editDeployConfig({
          projectName: this.projectName,
          buildName: buildName,
          paused: false,
          form: response
        })
      })
    },
    pauseDeploy (item) {
      /*
      this.deleteReplicationController({
        projectName: this.projectName,
        deployName: item.metadata.labels.deployment
      }).then((response) => {
        this.getDeployConfig({
          projectName: this.projectName,
          buildName: item.metadata.labels.deploymentconfig
        }).then((response) => {
          this.editDeployConfig({
            projectName: this.projectName,
            buildName: item.metadata.labels.deploymentconfig,
            paused: true,
            form: response
          }).then((response) => {
            this.deletePod({
              projectName: this.projectName,
              podName: item.metadata.name
            })
          })
        })
      })
      */
      this.getDeployConfig({
        projectName: this.projectName,
        buildName: item.metadata.labels.deploymentconfig
      }).then((deployconfig) => {
        this.editDeployConfig({
          projectName: this.projectName,
          buildName: item.metadata.labels.deploymentconfig,
          paused: true,
          form: deployconfig
        }).then((response) => {
          this.deleteReplicationController({
            projectName: this.projectName,
            deployName: item.metadata.labels.deployment
          }).then((response) => {
            this.deletePod({
              projectName: this.projectName,
              podName: item.metadata.name
            })
          })
        })
      })
    },
    testTemplate (templateName) {
      this.logText = ''
      this.getTemplate(templateName).then((t) => {
        this.logText += JSON.stringify(t)
      })
    },
    showImageStream (buildName) {
      this.logText = ''
      this.getImageStream({
        projectName: this.projectName,
        buildName: buildName
      }).then((i) => {
        this.logText += JSON.stringify(i)
      })
    },
    createBuild (templateName) {
      this.createBuildConfig({
        projectName: this.projectName,
        form: createBuildConfigForm
      })
    },
    createDeploy (buildName) {
      this.createDeployConfig({
        projectName: this.projectName,
        form: createDeployConfigForm
      })
    },
    delBuild (buildName) {
      this.deleteBuild({
        projectName: this.projectName,
        buildName: buildName
      })
    },
    getLog (buildName) {
      this.logText = ''
      this.getBuildLog({
        projectName: this.projectName,
        buildName: buildName
      }).then((log) => {
        this.logText = log
      })
    },
    getDetail (buildName) {
      /*
        customStrategy:
        dockerStrategy:
        jenkinsPipelineStrategy:
        sourceStrategy:
      */
      this.logText = ''
      const index = this.builds.items.findIndex(i => {
        return i.metadata.name === buildName
      })
      let spec = this.builds.items[index].spec
      let status = this.builds.items[index].status
      this.logText = `
    ` + buildName + `
    Status =====================
      Status:` + status.phase + `
      Started:` + status.startTimestamp + `
      Duration:` + status.duration + `
    
    Configuration =====================
      Build Strategy: ` + spec.strategy.type + `
      Builder Image:` + spec.strategy.dockerStrategy.from.name + `
      Type:` + spec.source.type + `
      Source Repo:` + spec.source.git.uri + `
      Source Ref:` + spec.source.git.ref + `
      Source Commit:` + spec.revision.git.commit + `
      ` + spec.revision.git.message + ` authored by ` + spec.revision.git.author.email + `
      Output Image: ` + spec.output.to.name
    },
    delBuildConfig (configName) {
      this.deleteBuildConfig({
        projectName: this.projectName,
        configName: configName
      })
    },
    delDeployConfig (configName) {
      this.deleteDeployConfig({
        projectName: this.projectName,
        configName: configName
      })
    },
    getPodLogMsg (podName) {
      this.logText = ''
      this.getPodLog({
        projectName: this.projectName,
        podName: podName
      }).then((log) => {
        this.logText = log
      })
    },
    getPodDetail (podName) {
      this.logText = ''
      const index = this.pods.items.findIndex(i => {
        return i.metadata.name === podName
      })
      let metadata = this.pods.items[index].metadata
      let spec = this.pods.items[index].spec
      let status = this.pods.items[index].status
      this.logText = `
        ` + podName + `
        ` + metadata.annotations['openshift.io/deployment-config.latest-version'] + `
        Status =====================
          Status:` + status.phase + `
          Started:` + status.startTime + `
          IP:` + status.podIP + `
          Node:` + spec.nodeName + `
        
        Container =====================
        ` + JSON.stringify(status.containerStatuses) + `
        
        Volumes =====================
        ` + JSON.stringify(spec.volumes)
    }
  },
  watch: {
    projectName: {
      immediate: true,
      handler: function () {
        // console.log('projectName changed=' + this.$route.params.projectName)
      }
    }
  }
}
</script>

<style scoped></style>
